import React, { Component } from 'react';
import Card from './ui/card';
import AddCard from './ui/add_card';
import AddBtn from './ui/AddBtn';
import { Link } from 'react-router-dom';
import $ from 'materialize-css/node_modules/jquery/dist/jquery';
import Api from './api.js';

export default class MyStreams extends Component {
  constructor(props) {
    super(props);
    this.state = {
      publisherID: '',
      imgUrl: '',
      channelName: '',
      channels: [],
      medias: []
    };

    this.requestApi = this.requestApi.bind(this);
    this.createChannel = this.createChannel.bind(this);
    this.upload = this.upload.bind(this);
  }

  componentDidMount() {
    // Initialize
    $(".button-collapse").sideNav();
    $('.modal').modal();
    //console.log('USER', this.props.user);
    // Initialize collapsible (uncomment the line below if you use the dropdown variation)
    this.requestApi();
    //$('.collapsible').collapsible();
  }

  upload(event) {
    if (!event.target.files[0]) {
      return;
    }
    Api.uploadFile(event.target.files[0], this.createChannel);
  }

  createChannel(uploadResData){


    var data = {
      profileImage: JSON.stringify(uploadResData.responseData),
      channelName: 'peti new channel',
      publisher: this.state.publisherID,
      category: JSON.stringify({"id":"58ca8d6ab98119002490f8c1","name":"News"}),
      apiKey: '40f0216565d0bfa250a88fb7e2c289ab'
    };
    var apiToken = '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6e72c047a87ff12e91e9b7';
    Api.createChannel(apiToken, data, console.log);
    this.setState({imgUrl: uploadResData.responseData.imageSourceURLSigned});
  }

  requestApi() {

    Api.getUserChannels(this.props.user.security.token, (data)=>{
      data.responseData[0].channelList.reverse();

      var fetchedData  = data.responseData[0].channelList.map((channel)=>{
        return {
          channelName: channel.channelName,
          _id: channel._id,
          imgSrc: channel.profileImage.imageSourceURLSigned || channel.profileImage.signedUrl
        };});

        this.setState({channels: fetchedData});

        this.setState({publisherID: data.responseData[0]._id});
      });
    }



    renderCards() {

      return this.state.channels.map((channel)=>{
        //console.log('in renderCards', channel);
        return (<Link key={channel._id} to={'/stream/'+channel._id}><Card mediaType='image' src={channel.imgSrc} name={channel.channelName} id={channel._id}/></Link>);
      });
    }


    render() {
      return (
        <div className="container">
          <h3>My streams</h3>

          <AddBtn/>

          {/*<form action="#">
            <div className="file-field input-field">
              <div className="btn">
                <span>File</span>
                <input type="file" onChange={this.upload}/>
              </div>
              <div className="file-path-wrapper">
                <input className="file-path validate" type="text" placeholder="Drag and drop your media here or click upload"/>
              </div>
            </div>
          </form>

          <a className="waves-effect waves-light btn" href="#modal1">Modal</a>
          */}


          <div id="modal1" className="modal modal-fixed-footer">
            <div className="modal-content">
              <h4>Modal Header</h4>
              <p>A bunch of text</p>
            </div>
            <div className="modal-footer">
              <a href="#!" className="modal-action modal-close waves-effect waves-green btn-flat ">Agree</a>
            </div>
          </div>




          <div className="row">
            <form className="col s12">
              <div className="row">
                <div className="input-field col s12">
                  <i className="material-icons prefix">search</i>
                  <input id="icon_prefix" type="text" className="validate"/>
                  <label htmlFor="icon_prefix">Search streams in My streams</label>
                </div>
              </div>
            </form>
          </div>

          <label>What's in your mind?</label><br/>
          <div className="row">
            <div className="col s12 m4">
              <div className="dropify-wrapper"><div className="dropify-message"><span className="file-icon"></span>
              <p>Drag and drop a file here or click</p>
              <p className="dropify-error">Sorry, this file is too large</p>
            </div><input onChange={this.upload} type="file" id="input-file-now" className="dropify" data-default-file=""/>
            <button type="button" className="dropify-clear">Remove</button><div className="dropify-preview"><span className="dropify-render"></span><div className="dropify-infos">
              <div className="dropify-infos-inner"><p className="dropify-filename"><span className="file-icon"></span> <span className="dropify-filename-inner"></span></p>
              <p className="dropify-infos-message">Drag and drop or click to replace</p></div></div></div></div>
            </div>
            </div>

          <label>All</label><br/>

          <div className="row">

            <div className="col s12 cards-container">
            {this.renderCards()}
            <Link to="/stream/idcghjmghjk"><Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/></Link>
            <Link to="/stream/idcghjmghjk"><Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/></Link>
            <Link to="/stream/idcghjmghjk"><Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/></Link>
            <Link to="/stream/idcghjmghjk"><Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/></Link>

            </div>
          </div>
        </div>
      );
    }
  }



