import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import $ from 'materialize-css/node_modules/jquery/dist/jquery';

export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      channelName: ''
    };
  }

  componentDidMount() {
    // Initialize collapse button
    $(".button-collapse").sideNav({
      menuWidth: 300, // Default is 300
      closeOnClick: true, // Closes side-nav on <a> clicks, useful for Angular/Meteor
      draggable: true, // Choose whether you can drag to open on touch screens,
    });
  }

  getContentSrc() {
    return 'https://emogo-media.s3.amazonaws.com/1498776961787_catface.png?AWSAccessKeyId=AKIAI5MUDCK6XYWKGAKA&Expires=1499381762&Signature=iRhuq6BoCKOAiQ7FjXnFIdS%2Fshs%3D';
  }

  render() {
    return (

      <header>
        <div className="navbar-fixed">
        <nav className="orange lighten-3">
          <div className="nav-wrapper">
            <a href="#!" className="brand-logo" style={{ "paddingLeft": "10px"}}><i className="material-icons">cloud</i>Thought Stream</a>
            <a href="#" data-activates="slide-out" className="button-collapse"><i className="material-icons">menu</i></a>

            <ul className="right hide-on-med-and-down">
              <li><a href="sass.html"><i className="material-icons">search</i></a></li>
              <li><a href="badges.html"><i className="material-icons">view_module</i></a></li>
              <li><a href="collapsible.html"><i className="material-icons">refresh</i></a></li>
              <li><a href="#"><i className="material-icons">menu</i></a></li>
            </ul>


          </div>
        </nav>
        </div>
        <ul id="slide-out" className="side-nav">
          <li><div className="user-view">
            <div className="background" style={{"backgroundColor": "#ffcc80"}}>
              {/*<img src="images/office.jpg" alt="office"/>*/}
            </div>
            <a href="#!user"><img className="circle" src={this.props.user.photo.url || 'http://lorempixel.com/60/60/cats/'}/></a>
            <a href="#!name"><span className="white-text name">{this.props.user.firstName}</span></a>
            <a href="#!email"><span className="white-text email">{this.props.user.emailID}</span></a>
          </div></li>
          <li><Link className="waves-effect" to="/stream"><i className="material-icons">cloud</i>My streams</Link></li>
          <li><Link className="waves-effect" to="/library"><i className="material-icons">view_module</i>Library</Link></li>
          <li><div className="divider"></div></li>
          <li><a className="waves-effect" href="#!">Setting</a></li>
          <li><a className="waves-effect" href="#!">Support</a></li>
        </ul>
      </header>



    );
  }
}



