import React, { Component } from 'react';
import MediaCard from './ui/MediaCard';
import AddCard from './ui/add_card';
import AddBtn from './ui/AddBtn';
import $ from 'materialize-css/node_modules/jquery/dist/jquery';
import Api from './api.js';

export default class Streams extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showPreview: false,
      channelName: '',
      medias: []
    };

    this.renderCardsDate = this.renderCardsDate.bind(this);
    this.requestApi = this.requestApi.bind(this);
    this.createContent = this.createContent.bind(this);
    this.upload = this.upload.bind(this);
  }

  componentDidMount() {
    // Initialize collapse button
    $(".button-collapse").sideNav();

    // Initialize collapsible (uncomment the line below if you use the dropdown variation)

    this.requestApi();

    //$('.collapsible').collapsible();
  }

  componentDidUpdate() {
    $('.slider').slider({
      height: window.screen.height,
      interval: 5000
    });
  }

  upload(event) {
    if (!event.target.files[0]) {
      return;
    }
    Api.uploadFile(event.target.files[0], this.createContent);
  }

  createContent(uploadRes) {
    console.log('uploadRes in createContent', uploadRes);
    var apiToken = '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6e72c047a87ff12e91e9b7';
    var data = {
      apiKey: '40f0216565d0bfa250a88fb7e2c289ab',
      media: JSON.stringify([{media: {...uploadRes.responseData}, mediaThumbnail: {...uploadRes.responseData} }]),
      channelID: this.props.match.params.id,
      publisher: this.props.user.publisherID
    };
    console.log('data I made in createContent', data);
    Api.createContent(apiToken, data, console.log);

  }

  requestApi() {
    console.log('this.props.match.params.id ', this.props.match.params.id);
    //console.log('run requestApi props', this.props.location);
    let token = '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6e7ccb42af73fe2a94efb7';
    Api.getContent(this.props.match.params.id, token, this.props.user.publisherID, this.renderCardsDate);
    /*$.ajax({
    url: "http://emogo-manage-content-service-test.us-east-2.elasticbeanstalk.com/channels/595132f6a9588a002404b980/content",
    headers: { 'app-type': 'iOS', 'Authorization': '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6f7cc544af7efd2a94eab7'},
    type: "GET",
    success: this.renderCardsDate
    });*/
  }

  renderCardsDate(data) {
    if (!data.responseData || !data.responseData.responseData || data.responseData.responseData.length === 0) {
      return;
    }
    var channelName = data.responseData.responseData[0].channelName;
    var medias = data.responseData.responseData[0].contentList.map((content)=> {
      return {
        key: content._id,
        _id: content._id,
        name: content.media.name,
        mediaType: content.media.mediaType,
        src:content.media.imageSourceURLSigned
      };
    });
    this.setState({channelName: channelName});
    this.setState({medias: medias});
  }

  renderCards() {
    return this.state.medias.map((media)=>{
      return (<MediaCard {...media} />);
    });
  }

  renderFake

  render() {
    return (
      <div className="container">
        <AddBtn/>

        <h1><img className="circle backup_picture" style={{"width":"50px", "height": "50px"}} src="https://emogo-media.s3.amazonaws.com/1498776961787_catface.png?AWSAccessKeyId=AKIAI5MUDCK6XYWKGAKA&amp;Expires=1499381762&amp;Signature=iRhuq6BoCKOAiQ7FjXnFIdS%2Fshs%3D"/> Cat's collection</h1>

        <label>collaborator</label><br/>
        <div className="chip">
          <img src="http://lorempixel.com/50/50/cats/" alt="Contact Person"/>
          Jane Doe
        </div>
        <div className="chip">
          <img src="http://lorempixel.com/50/50/cats/" alt="Contact Person"/>
          Jane Doe
        </div>
        <br/><br/>
        <label>What's in your mind?</label><br/>
        <div className="row">
          <div className="col s12 m4">
            <div className="dropify-wrapper"><div className="dropify-message"><span className="file-icon"></span>
            <p>Drag and drop a file here or click</p>
            <p className="dropify-error">Sorry, this file is too large</p>
          </div><input onChange={this.upload} type="file" id="input-file-now" className="dropify" data-default-file=""/>
          <button type="button" className="dropify-clear">Remove</button><div className="dropify-preview"><span className="dropify-render"></span><div className="dropify-infos">
            <div className="dropify-infos-inner"><p className="dropify-filename"><span className="file-icon"></span> <span className="dropify-filename-inner"></span></p>
            <p className="dropify-infos-message">Drag and drop or click to replace</p></div></div></div></div>
          </div>
        </div>
        <div className="row">
          <label>Stream</label>
          <a className="right waves-effect waves-light btn orange lighten-2" onClick={()=>{this.setState({showPreview: !this.state.showPreview})}}><i className="material-icons left">visibility</i>preview</a>
          {
            this.state.showPreview ?

            <div>
              <div className="modal-overlay" onClick={()=>{this.setState({showPreview: !this.state.showPreview})}} id="materialize-modal-overlay-1" style={{"zIndex": "1000", "display": "block", "opacity": "1", "position": "fixed"}}></div>
              <a className="white-text text-darken-3 btn-flat" style={{"zIndex": "1001", "position": "fixed", "top": "0", "right": "0"}} onClick={()=>{this.setState({showPreview: !this.state.showPreview})}}><i className="material-icons right">close</i></a>
              <div className="slider" onClick={()=>{$('.slider').slider('next');}} style={{"zIndex": "1000", "position": "fixed", "width": "340px", "height": window.screen.height + "px", "top": "0", "right": "0", "left": "0", "margin": "auto"}}>
                <ul className="slides">
                  <li>
                    <img src="http://lorempixel.com/200/500/cats/1"/>
                    <div className="caption center-align">
                      <h3>This is our big Tagline!</h3>
                      <h5 className="light grey-text text-lighten-3">Here's our small slogan.</h5>
                    </div>
                  </li>
                  <li>
                    <img src="http://lorempixel.com/200/500/cats/2"/>
                    <div className="caption left-align">
                      <h3>Left Aligned Caption</h3>
                      <h5 className="light grey-text text-lighten-3">Here's our small slogan.</h5>
                    </div>
                  </li>
                  <li>
                    <img src="http://lorempixel.com/200/500/cats/3"/>
                    <div className="caption right-align">
                      <h3>Right Aligned Caption</h3>
                      <h5 className="light grey-text text-lighten-3">Here's our small slogan.</h5>
                    </div>
                  </li>
                  <li>
                    <img src="http://lorempixel.com/200/500/cats/4"/>
                    <div className="caption center-align">
                      <h3>This is our big Tagline!</h3>
                      <h5 className="light grey-text text-lighten-3">Here's our small slogan.</h5>
                    </div>
                  </li>
                </ul>
              </div>

            </div>
            : null
          }
        </div>
        <div className="row">
          {this.renderCards()}
          <MediaCard name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <MediaCard name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <MediaCard name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <MediaCard name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
        </div>
      </div>
    );
  }
}



