import React, { Component } from 'react';
import $ from 'materialize-css/node_modules/jquery/dist/jquery';

export default class Card extends Component {
  getContentSrc() {
    return 'https://emogo-media.s3.amazonaws.com/1498776961787_catface.png?AWSAccessKeyId=AKIAI5MUDCK6XYWKGAKA&Expires=1499381762&Signature=iRhuq6BoCKOAiQ7FjXnFIdS%2Fshs%3D';
  }

  componentDidMount() {
    $(".backup_picture").on("error", function(){
        $(this).attr('src', 'http://lorempixel.com/200/400/cats/ImgNotAvalible');
    });
  }

  render() {
    var mediaType = {
      video: "video",
      image: "image"
    };

    return (


        <div className="card">
          <div className="card-image">
            { this.props.mediaType == mediaType.image ?
              <img src={this.props.src} className='backup_picture' style={{}} alt='ImgNotAvalible'/>
              :
              <video className="responsive-video" style={{}}>
                <source src={this.props.src} type="video/mp4"/>
              </video>

            }
            <span className="card-title">{this.props.name}</span>
          </div>
          {/*<div className="card-content">
            <p>I am a card.</p>
            </div>
            <div className="card-action">
            <a href="#">This is a link</a>
            </div>*/}
          </div>
        
      );
    }
  }



