import React, { Component } from 'react';
import './App.css';
import Landing from './Landing';
import Header from './Header.js';
import Main from './Main.js';
import Api from './api.js';
import $ from 'materialize-css/node_modules/jquery/dist/jquery';
//import 'materialize-css/dist/js/materialize.js';
//import 'materialize-css/dist/js/materialize.min.js';
//import 'materialize-css';
//var Materialize = require('materialize-css/dist/js/materialize.min.js');

//import {Materialize} from 'materialize-css/dist/js/materialize.js';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: null,
      sessionToken: null, // the user's token
      developerToken: '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6e72c047a87ff12e91e9b7',
      channelName: '',
      medias: []
    };

    this.renderCardsDate = this.renderCardsDate.bind(this);
    this.requestApi = this.requestApi.bind(this);
    this.login = this.login.bind(this);
    this.getPublisherID = this.getPublisherID.bind(this);
  }

  componentDidMount() {
    /*this.requestApi();

    Api.login('peter@emogotechnologies.com', 'marathas64', console.log);
    var APItoken = '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6e71c04da87ff82090e9b7';
    Api.getContent('594a8863a9588a002404aa87' , APItoken, console.log);*/
    //Api.getApiToken();
  }

  login(email, password) {
    console.log('login run');
    //'peter@emogotechnologies.com', 'marathas64'
    Api.login(email, password, (data)=>{
      console.log("login", data);
      if(!data.success) {
        //window.Materialize.toast(data.message[0].msg, 4000);
        return;
      }




      this.getPublisherID(data.responseData.userDetails, data.responseData.sessionToken);
    });
  }

  getPublisherID(user, sessionToken) {
    Api.getPublisherID(user.security.token, (publisherID)=>{
      //let user = {...this.state.user};
      user.publisherID = publisherID;
      //this.setState(user);

      this.setState({
        user: user,
        sessionToken: sessionToken
      });

    });
  }

  logout() {
    this.setState({user: null});
  }

  requestApi() {
    console.log('run requestApi');
    $.ajax({
      url: "http://emogo-manage-content-service-test.us-east-2.elasticbeanstalk.com/channels/595132f6a9588a002404b980/content",
      headers: { 'app-type': 'iOS', 'Authorization': '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6f7cc544af7efd2a94eab7'},
      type: "GET",
      success: this.renderCardsDate /*function(data) {
        console.log(data);
        this.renderCardsDate(data);
        }*/
      });
    }

    renderCardsDate(data) {
      console.log(data);
      if (!data.responseData || !data.responseData.responseData || data.responseData.responseData.length === 0) {
        return;
      }

      var channelName = data.responseData.responseData[0].channelName;
      var medias = data.responseData.responseData[0].contentList.map((content)=> {
        return {
          key: content._id,
          _id: content._id,
          name: content.media.name,
          mediaType: content.media.mediaType,
          src:content.media.signedUrl
        };
      });
      this.setState({channelName: channelName});
      this.setState({medias: medias});
    }


    getContentSrc() {
      return 'https://emogo-media.s3.amazonaws.com/1498776961787_catface.png?AWSAccessKeyId=AKIAI5MUDCK6XYWKGAKA&Expires=1499381762&Signature=iRhuq6BoCKOAiQ7FjXnFIdS%2Fshs%3D';
    }

    render() {
      return (
        <div className="App">
          { this.state.user ?
            <div className='userApp'>
              <Header user={this.state.user} sessionToken={this.state.sessionToken}/>,
              <Main user={this.state.user} sessionToken={this.state.sessionToken}/>
            </div>
            :
            <Landing login={this.login}/>
          }
        </div>
      );
    }
  }



