import React, { Component } from 'react';

export default class AddCard extends Component {


  getContentSrc() {
    return 'https://emogo-media.s3.amazonaws.com/1498776961787_catface.png?AWSAccessKeyId=AKIAI5MUDCK6XYWKGAKA&Expires=1499381762&Signature=iRhuq6BoCKOAiQ7FjXnFIdS%2Fshs%3D';
  }

  render() {
    return (
      <div className="col s12 m4">
        <div className="card-panel orange lighten-3">
          <h2 className="white-text center">
            <i className="material-icons" style={{"fontSize":"100px"}}>note_add</i> <br/>
              add new card
          </h2>
        </div>

      </div>
    );
  }
}



