import React, { Component } from 'react';
import Card from './ui/card';
import AddCard from './ui/add_card';
import $ from 'materialize-css/node_modules/jquery/dist/jquery';

export default class Library extends Component {
  constructor(props) {
    super(props);
    this.state = {
      channelName: '',
      medias: []
    };

    this.renderCardsDate = this.renderCardsDate.bind(this);
    this.requestApi = this.requestApi.bind(this);
  }

  componentDidMount() {
    // Initialize collapse button
    $(".button-collapse").sideNav();
    // Initialize collapsible (uncomment the line below if you use the dropdown variation)
    this.requestApi();
    //$('.collapsible').collapsible();
  }

  requestApi() {
    console.log('run requestApi');
    $.ajax({
      url: "http://emogo-manage-content-service-test.us-east-2.elasticbeanstalk.com/channels/595132f6a9588a002404b980/content",
      headers: { 'app-type': 'iOS', 'Authorization': '90b16e4f8b69d4616708be7abbf1ccb38a1e75f0f1961726e1f4e65f867e44204e1e4efadc9677c95c4d013a77850c14d8b225dd1afa5683c3c7e9db09ea8ec84a8be961c5bd6f7cc544af7efd2a94eab7'},
      type: "GET",
      success: this.renderCardsDate
    });
  }

  renderCardsDate(data) {
    if (!data.responseData || !data.responseData.responseData || data.responseData.responseData.length === 0) {
      return;
    }
    var channelName = data.responseData.responseData[0].channelName;
    var medias = data.responseData.responseData[0].contentList.map((content)=> {
      return {
        key: content._id,
        _id: content._id,
        name: content.media.name,
        mediaType: content.media.mediaType,
        src:content.media.signedUrl
      };
    });
    this.setState({channelName: channelName});
    this.setState({medias: medias});
  }

  renderCards() {

    return this.state.medias.map((media)=>{
      return (<Card {...media} />);
    });
  }


  render() {
    return (
      <div className="container">
        <h3>Library</h3>

        <div className="row">
          <form className="col s12">
            <div className="row">
              <div className="input-field col s12">
                <i className="material-icons prefix">search</i>
                <input id="icon_prefix" type="text" className="validate"/>
                <label htmlFor="icon_prefix">Search medias you posted</label>
              </div>
            </div>
          </form>
        </div>

        <div className="row">
          {this.renderCards()}
          <Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <Card name="sample" src="http://lorempixel.com/200/400/cats/" mediaType="image"/>
          <AddCard/>
        </div>
      </div>
    );
  }
}



